var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"no-list"},[_vm._v("\n    01:28\nMy first night at Langata Women Maximum Security Prison was the toughest.\n\nLangata Women's Prison (LWP) is the only all women's prison in Nairobi.  It is a big compound, but there are over 750 prisoners, 50 children accompanying their incarcerated mothers and there are over 400 staff that reside in the prison compound due to security. LWP is comprised of a Remand Centre for short sentences and those awaiting trial or sentencing, a Medium Security Section for slightly longer sentences, and then the Maximum Security for the convicted and what we would consider federal offenses and long-term sentences.  There is also an onsite Nursery School where the prison staff children attend, the children of the prisoners and also some outside children are permitted to attend.  Children are allowed to stay with their mothers until they are 4 years old.  If the mother is still in prison once the child turns 4 then the child is sent to a children's home until the mother is discharged. There are even children born there!  What a setting for a child. \n\nBehind Bars: A Look at Life in One of Kenya's Most Notorious Prisons, Destination Magazine\n\n02:14\nThe absurdity of it all was the arresting officer asking me to pay him 10,000 US dollars and the case would disappear. I refused.\n\nInterference with the criminal justice system is common practice in Kenya.  A popular saying, \"Why hire a lawyer when you can buy a judge?\" sums up many Kenyan's views of Judicial integrity.\n\nCorruption in Africa: The Kenyan Experience: a Handbook for Civic Education, Peter Anassi, 2004\n\n06:16\nA broken system that routinely vilifies the vulnerable, the poorest amongst us, people who cannot afford to pay bail or bribes.\n\nTransforming the Courts: Judicial Sector Reforms in Kenya, 2011-2015, Princeton University\n\n07:10\nLack of economic opportunities that pushes these women to petty survival crimes.\n\nKenyaâs rate of unemployment stands at three times that of the neighboring Uganda and Tanzania, also among the highest in the world - according to a World Bank report.\n\nKenya has the largest number of jobless youth in East Africa, Business Daily\n\nKenya: Joblessness Major Cause of Crime in Kenya's Slums, All Africa\n\n08:31\nWe go into the prisons, train them, give them skills, tools and support to enable them to be able to change their mindsets, their behaviors and their attitudes.\n\nAt Clean Start, we have adapted the award-winning Spear Course from Resurgo Trust UK, which has recently been awarded the Princess Royal Training Award for using executive coaching techniques.\n  ")])}
